import useApi from '@/hooks/useApi'
import { ref, defineProps, defineEmits, computed } from 'vue'
import router from '@/router'

const addContact = () => {
  const { fetch } = useApi()
  const data = ref({
    body: [{
      name: 'Рабочие дни'
    }, {
      name: 'Рабочие часы'
    }, {
      name: 'Обеденный перерыв'
    }]
  })
  const error = ref({})
  const add = () => {
    fetch('post', 'api/contacts', data.value, 'PostLoading')
      .then(() => router.push('/admin/contacts'))
      .catch((err) => (error.value = err.response.data))
  }
  return {
    data,
    error,
    add
  }
}

const contactForm = () => {
  const props = defineProps(['data', 'error', 'btnPrimeText'])
  const emit = defineEmits(['update:data'])

  const data = computed({
    get() {
      return props.data
    },
    set(value) {
      emit('update:data', value)
    }
  })
  return {
    data,
    btnText: props.btnPrimeText
  }
}

export { addContact, contactForm }
